/* About Style */

hr.style-two {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.simpleAbout {
  background: #171717;
  color: #fff;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.simpleText {
  width: 62.5%;
  margin: auto;
  font-size: 22.5px;
  line-height: 1.6;
  color: #bcbcbc ;
}

/* About Team Card */

.team {
  background: #121212;
  color: #000000;
  padding: 50px 10%;
}

.card {
  max-width: 300px;
  transition: 0.3s;
  background-color: #121212 !important;
  box-shadow: 0px 0px 10px 0px #000;
  border-radius: 20px;
  margin: 0 auto 50px;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 20px 0px #000;
}

.card img {
  height: 300px;
  width: 100%;
}

/* Work */


.teamWork {
  background: #171717;
  color: #fcfcfc;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding: 50px 0;
}

.work {
  width: 380px;
  height: 265px;
  justify-content: space-around;
  flex-wrap: wrap;
  background: #007FFF;
  color: #fcfcfc;
  padding: 1.2rem;
  border-radius: 12px;
  margin-top: 10px;
  margin-bottom: 5px;
  font-weight: 400;
  margin-left: 5px;
  margin-right: 5px;
}

.sty3{
  position: relative;
  top: 45px;
}


.sty1:hover{
  color: #fff;
  text-decoration: none;
}


.sty2:hover{
  color: #fff;
  text-decoration: none;
}

.sty3:hover{
  color: #fff;
  text-decoration: none;
}

.aa {
  text-decoration: none !important;
  background: #fff;
  padding: 5px 10px;
}

/* Career */
.career{
  background: #060606;
  color: #eee;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 0 0;
}

.aboutButton{
  background: #060606;
  min-height: 1vh;
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
}

.careerButton{
  padding: 1.2rem;
  background: #0155dc;
  color : #fcfcfc;
  border: none;
  outline: none;
  cursor: pointer;
  margin-bottom: 20px;
}

.careerFont{
  font-size: 18px;
  margin-left: 10%;
  margin-right: 10%;
}

/* Media Query */

@media screen and (max-width: 1024px) {
  .simpleText {
      width: 80%;
      margin: auto;
  }
}

@media screen and (max-width: 768px) {
  .simpleText {
      width: 70%;
      margin: auto;
  }
}

@media screen and (max-width: 425px) {
  .simpleText {
      width: 90%;
      margin: auto;
  }
}


@media screen and (max-width: 320px) {
.work{
  height: unset;
}

.sty3{
  position: relative;
  top: 10px;
}
.card {
  margin-left: -37px;
}
}