  left: 20%;
  transform: translate(-50%, -50%);
  font-family: "Montserrat";
}

.stext1 {
  position: relative;
  left: 20%;
  animation: 5s movingBottomToTop;
}

.stext2 {
  font-weight: bold;
  position: relative;
  top: -40px;
  left: 21%;
  font-size: 1.2rem;
}

.exp-btn {
  font-weight: bold;
  position: relative;
  top: -10px;
  left: 21%;
  font-size: 1.2rem;
  background-color: #fff;
  border: 1px solid #000;
  padding: 10px 40px;
  transition: 0.3s;
  cursor: pointer;
}

.exp-btn:hover {
  background-color: #000;
  color: #fff;
  box-shadow: 0px 5px 5px #121212;
}

.exp-btn:hover .angle-arr {
  background-color: #fff;
  color: #000;
  transition: 0.3s;
}

.angle-arr {
  background-color: #000;
  color: #fff;
  border-radius: 50%;
  padding: 5px 10px;
  margin-left: 10px;
}

.readButton {
  width: 90%;
  border: 1px solid #000;
  color: #000;
  background-color: #fff;
  transition: 0.4s;
  padding: 10px 0;
  cursor: pointer;
  font-weight: bold;
  display: none;
}

.readButton:hover {
  color: #fff;
  background-color: #000;
}

.readButton2 {
  border: 1px solid #000;
  color: #000;
  background-color: red;
  transition: 0.4s;
  padding: 10px 0;
  cursor: pointer;
  font-weight: bold;
  margin-left: 20px;
  padding: 10px 30px;
  font-weight: bold;
}

.readButton2:hover {
  color: #fff;
  background-color: #000;
}

.HomeCnct-btn {
