@import 'bootstrap/dist/css/bootstrap.min.css';


.BlogTopSec {
    padding: 6% 20%;
    background-color: #171717;
}
.BlogTopHeadText {
    font-size: 5rem;
}
.BlogCarousel {
    padding: 0px 20% 50px;
    background-color: #171717;
}
.carousel-img {
    height: 400px;
}
.BlogCard {
    background-color: #121212;
    padding: 5% 15%;
}
.BlogSingleCard {
    margin-top: 5%;
}

@media(max-width: 768px) {
    .BlogTopSec {
        padding: 10% 20%;
    }
}
@media(max-width: 425px) {
    .BlogTopSec {
        padding: 15% 10%;
    }
    .BlogTopHeadText {
        font-size: 3rem;
    }
    .BlogCarousel {
        padding: 0px 10% 50px;
    }
    .BlogCard {
        padding: 5% 10%;
    }
    .BlogSingleCard {
        margin-top: 10%;
    }
}
@media(max-width: 325px) {
    .BlogTopSec {
        padding: 15% 5%;
    }
    .BlogTopHeadText {
        font-size: 2.5rem;
    }
    .BlogCarousel {
        padding: 0px 5% 50px;
    }
    .BlogCard {
        padding: 5% 5%;
    }
}