@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.Navimg {
  position: absolute;
  width: 400px;
  height: 100%;
  right: 5%;
  top:8%;
}
header {
    position: relative;
    height: 580px;
    overflow: hidden;
  }  
.header__bg {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(#161616, #161616);
    transform: skewY(-10deg);
    transform-origin: top left;
    box-shadow: 0px 0px 10px gray;
  }
  .slogan {
      position: absolute;
      top: 180px;
      left: 10%;      
  }
  .SloganText1 {
      padding-left: 20px;
      border-left: 3px solid #007FFF;
      font-size: 3rem;
  }
  .SloganText3 {
    padding-right: 20px;
    border-right: 3px solid #007FFF;
    padding-left: 20px;
    font-size: 3rem;
}
.SloganText2 {
    padding-left: 20px;
    font-size: 3rem;
}

.overlay {
    height: 100%;
    width: 0%;
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    background-color: #161616;
    overflow-x: hidden;
    transition: 0.5s;
  }
  
  .overlay-content {
    position: relative;   
    top: 18%;
    width: 100%;
    text-align: center;
    
  }
  
  .overlay .link {
    padding: 8px;
    text-decoration: none;
    font-size: 28px;
    color: #bcbcbc;
    display: block;
    transition: 0.3s;
    padding-bottom: 20px;
  }
  
  .overlay .link:hover, .overlay .link:focus {
    color: #007FFF;
    /* background-color: #1167b2; */
  }
  
  .overlay .closebtn {
    position: absolute;
    right: 45px;
    top: -10px;
    font-size: 3.5rem;
  }
  .expl-btn {
    background-color: #0080ffa2;
    color: #eee;
    border: 1px solid #0080ffa2;
    padding: 10px 50px;
    cursor: pointer;
    font-weight: bold;
    border-radius: 5px;
    margin-left: 25px;
    transition: 0.3s;
  }
  .expl-btn:hover {
    background-color: #171717;
    color: #007FFF;
    border: 1px solid #007FFF;
  }
  .expl-btn:hover .exp-arrow-btn{
    background-color: #007FFF;
    color: #eee;
  }
  .exp-arrow-btn {
    background-color: #eee;
    color: #007FFF;
    margin-left: 10px;
    padding: 2px 5px;
    border-radius: 50%;
    position: relative;
    top: 1  px;
  }



  @media(max-width: 768px) {
    .Navimg {
      position: absolute;
      width: 300px;
      height: 100%;
      right: 0%;
      top:8%;
    }
    .NavSloganText2 {
      letter-spacing: 0px;
    }
    .overlay .link {
      padding: 8px;
      font-size: 26px;
      padding-bottom: 20px;
    }
    .overlay .closebtn {
      position: absolute;
      right: 45px;
      top: 0px;
      font-size: 3rem;
    }
  }

  @media screen and (max-height: 450px) {
    .overlay a {font-size: 20px}
    .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
    }
   
  }
  @media(max-width: 425px) {
    .NavSloganText2 {
      color: #eee;
      position: relative;
      top: -20px;
      right: 60%;
      font-size: 13px;
     }
     .Navimg {
      position: absolute;
      width: 220px;
      height: 500px;
    }
    .SloganText1,.SloganText2,.SloganText3 {
      font-size: 2rem;
    }
    .slogan {
      top: 150px;
      left: 5%;      
  }
  .expl-btn {
    padding: 10px 30px;
    margin-left: 15px;
    margin-top: 20px;
  }
  }
  @media(max-width: 375px) {
    .NavSloganText2 {
      color: #eee;
      position: relative;
      top: -20px;
      right: 40%;
      font-size: 13px;
     }
     .Navimg {
      position: absolute;
      width: 180px;
      height: 400px;
      top: 20%;
    }
    .SloganText1,.SloganText2,.SloganText3 {
      font-size: 1.7rem;
    }
    .slogan {
      top: 180px;
      left: 5%;      
  }
  .expl-btn {
    padding: 5px 20px;
    margin-left: 15px;
    margin-top: 20px;
  }
  .navText {
    right: 70px !important;
  }
  .hamburger {
    right: 20px !important;
  }
  }
  @media(max-width: 345px) {
    .NavSloganText2 {
      color: #eee;
      position: relative;
      top: -20px;
      right: 10%;
      font-size: 13px;
     }
     .Navimg {
      position: absolute;
      width: 150px;
      height: 350px;
      top: 20%;
    }
    .SloganText1,.SloganText2,.SloganText3 {
      font-size: 1.5rem;
    }
    .slogan {
      top: 150px;
      left: 5%;      
  }
  .expl-btn {
    padding: 5px 20px;
    margin-left: 15px;
    margin-top: 20px;
  }
  .navText {
    right: 60px !important;
    top: 22px !important;
  }
  .hamburger {
    right: 20px !important;
    top: 15px !important;
  }
  .overlay .closebtn {
    top: -20px;

  }
  }