@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cinzel&display=swap");

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  padding: 0;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */


.bold {
  font-weight: bold;
}

.bg-image {
  background-image: url("https://wallpapercave.com/wp/wp2533041.jpg");
  height: 500px;
}

.navbar {
  padding-top: 20px;
  font-family: "Montserrat", sans-serif;
  height: 600px;
  background-color: #000;
  clip-path: polygon(35% 0, 100% 0, 100% 100%);
  /* background-image: linear-gradient(#000, #000);
    transform: skewY(-10deg);
    transform-origin: top left; */
}

.logo-link {
  position: absolute;
  top: 25px;
  left: 5%;
  box-shadow: 0px 0px 10px 5px #000;
  padding: 3px;
  border-radius: 50px;
}

.logo {
  width: 170px;
  height: 60px;
}

.navlink {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  display: inline;
  margin-right: 20px;
  transition: 0.4s;
  padding: 10px;
}

.navlink:hover {
  background-color: #fff;
  color: #000;
}

.nav-links {
  float: right;
  margin-top: 10px;
}

.slogan-text {
  position: absolute;
  top: 50%;
  left: 20%;
  transform: translate(-50%, -50%);
  font-family: "Montserrat";
}

.stext1 {
  position: relative;
  left: 20%;
  animation: 5s movingBottomToTop;
}

.stext2 {
  font-weight: bold;
  position: relative;
  top: -40px;
  left: 21%;
  font-size: 1.2rem;
}

.exp-btn {
  font-weight: bold;
  position: relative;
  top: -10px;
  left: 21%;
  font-size: 1.2rem;
  background-color: #fff;
  border: 1px solid #000;
  padding: 10px 40px;
  transition: 0.3s;
  cursor: pointer;
}

.exp-btn:hover {
  background-color: #000;
  color: #fff;
  box-shadow: 0px 5px 5px #121212;
}

.exp-btn:hover .angle-arr {
  background-color: #fff;
  color: #000;
  transition: 0.3s;
}

.angle-arr {
  background-color: #000;
  color: #fff;
  border-radius: 50%;
  padding: 5px 10px;
  margin-left: 10px;
}

.readButton {
  width: 90%;
  border: 1px solid #000;
  color: #000;
  background-color: #fff;
  transition: 0.4s;
  padding: 10px 0;
  cursor: pointer;
  font-weight: bold;
  display: none;
}

.readButton:hover {
  color: #fff;
  background-color: #000;
}

.readButton2 {
  border: 1px solid #000;
  color: #000;
  background-color: red;
  transition: 0.4s;
  padding: 10px 0;
  cursor: pointer;
  font-weight: bold;
  margin-left: 20px;
  padding: 10px 30px;
  font-weight: bold;
}

.readButton2:hover {
  color: #fff;
  background-color: #000;
}

.HomeCnct-btn {
  color: #007FFF;
  font-weight: bold;
  padding: 15px 30px;
  background-color: #eee;
  transition: 0.3s;
  cursor: pointer;
  text-decoration: none;
}

.HomeCnct-btn:hover {
  background-color: #007FFF;
  color: #eee;
  text-decoration: none;
}

.homeCard {
  border-radius: 10px;
  border-bottom: 7px solid #007FFF;
  box-shadow: 0px 0px 10px 0px #000;
}

.homeCard:hover {
  box-shadow: 0px 0px 10px 3px #000;
  margin-top: -10px;
}

.box {
  position: relative;
  max-width: 600px;
  width: 100%;
  height: 350px;
}

/* common */
.ribbon {
  width: 150px;
  height: 150px;
  overflow: hidden;
  position: absolute;
}

.ribbon::before,
.ribbon::after {
  position: absolute;
  z-index: -1;
  content: "";
  display: block;
  border: 5px solid #000;
}

.ribbon span {
  position: absolute;
  display: block;
  width: 225px;
  padding: 8px 0;
  background-color: #007FFF;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  color: #fff;
  font: 700 18px/1 "Lato", sans-serif;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  text-transform: uppercase;
  text-align: center;
}

/* top right*/
.ribbon-top-right {
  top: -10px;
  right: -10px;
}

.ribbon-top-right::before,
.ribbon-top-right::after {
  border-top-color: transparent;
  border-right-color: transparent;
}

.ribbon-top-right::before {
  top: 0;
  left: 0;
}

.ribbon-top-right::after {
  bottom: 0;
  right: 0;
}

.ribbon-top-right span {
  left: -8px;
  top: 30px;
  transform: rotate(45deg);
}

.footerlink {
  color: #bcbcbc;
}

.footerlink:hover {
  padding-left: 10px;
  color: #fff;
}

.footerSlink {
  color: #bcbcbc;
}

.footerSlink:hover {
  color: #fff;
}

.HomeServicesSec {
  padding: 20px 5%;
}

.Child-sec {
  margin: 20px 10%;
}

/* SERVICES PAGE  */

.LMore-services-btn {
  background: none;
  padding: 10px 30px;
  cursor: pointer;
  margin-left: 4%;
  transition: 0.4s;
  border: 1px solid #fff;
  color: #fff;
  font-weight: bold;
  font-size: 1rem;
}

.LMore-services-btn:hover {
  background-color: #fff;
  color: #4955bb;
  padding: 10px 30px;
  cursor: pointer;
  margin-left: 4%;
  transition: 0.4s;
}

.LMore-services-icon {
  background-color: #fff;
  padding: 2px 5px 0px 5px;
  border-radius: 50%;
  color: #4955bb;
  margin-left: 10px;
  font-size: 16px;
  position: relative;
  top: 2px;
  transition: 0.4s;
}

.LMore-services-btn:hover .LMore-services-icon {
  background-color: #4955bb;
  color: #fff;
}

.ServicesCard {
  background: none;
  border-bottom: 5px solid #633fd5;
}

.ServicesCard:hover {
  box-shadow: 0px 0px 20px 0px #000;
  position: relative;
  z-index: 1;
}

.servicesCardGrid {
  margin: 5% 10%;
}

.ServicesGIT-btn {
  background: none;
  padding: 10px 50px;
  cursor: pointer;
  transition: 0.4s;
  border: 1px solid #fff;
  color: #fff;
  font-weight: bold;
  font-size: 1rem;
  border-radius: 5px;
}

.servicessubText {
  font-size: 2rem;
}

.ServicesGIT-btn:hover {
  background-color: #fff;
  color: #4955bb;
  cursor: pointer;
  transition: 0.4s;
}

.servicesBottomSec {
  font-size: 3rem;
}

/* CAREER PAGE  */

.careerTopSec {
  padding: 0% 20%;
  margin-top: 3%;
}

.AtWork {
  box-shadow: 0px 0px 20px 0px #000;
  border-radius: 20px;
}

.arrowAnimation {
  position: relative;
  animation: ArrowAnimate 1.5s linear infinite;
}

.JobCard {
  box-shadow: 0 0 14px 0 rgb(0 0 0 / 40%);
  cursor: pointer;
}

.JobCard:hover {
  transform: scale(1.05);
  box-shadow: 0 0 14px 0 rgb(0 0 0 / 60%)
}


/* MOBILE FOOTER */
.MuiSvgIcon-root {
  color: #eee;
}

.mobile-footer {
  display: none;
}

/* ANIMATIONS  */
@keyframes movingBottomToTop {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes ArrowAnimate {
  0% {
    top: 0
  }

  50% {
    top: 15px
  }

  to {
    top: 0
  }
}

/* Media Query */
@media screen and (max-width: 1024px) {
  .nav-links {
    font-size: 8px;
    margin-top: 25px;
  }

  .navbar {
    padding-top: 20px;
    font-family: "Montserrat", sans-serif;
    height: 500px;
    background-color: #000;
    clip-path: polygon(16% 0, 100% 0, 100% 100%);
  }

  .slogan-text {
    position: absolute;
    top: 50%;
    left: 20%;
    transform: translate(-50%, -50%);
    font-family: "Montserrat";
  }

  .stext1 {
    position: relative;
    left: 25%;
    animation: 5s movingBottomToTop;
  }

  .stext2 {
    font-weight: bold;
    position: relative;
    top: -40px;
    left: 50%;
    font-size: 1.2rem;
  }
}

