*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Title */

/* Card */

.contact {
  background: #121212;
  margin-top: 50px;
  padding-top: 20px;
}

.contactcard {
  margin-bottom: 20px;
  border-radius: 12px;
}

/* Form */

.contactform{
background: #171717;
color: #fcfcfc;
margin: 50px 2px;
border-radius: 6px;
padding: 20px;
box-shadow: 0px 0px 10px 0px #000;
}

.btnn{
  position: relative;
  left: 200px;
  background: #007FFF;
  color: #fcfcfc;
  outline: none;
  border: none;
  padding: 10px 50px;
}
.formtext{
  text-align: center;
  margin: 90px 10px 0;
}

.down{
  width: 5%;
}

@media screen and (max-width : 1024px){ 
.btnn{
  position: relative;
  left: 170px;
}
}

@media screen and (max-width : 768px){ 
  .btnn{
    position: relative;
    left: 270px;
  }
  }

  
@media screen and (max-width : 425px){ 
  .btnn{
    position: relative;
    left: 120px;
  }
  
.down{
  width: 20%;
}
  }

  
@media screen and (max-width : 375px){ 
  .btnn{
    position: relative;
    left: 100px;
  }
  }

  
@media screen and (max-width : 320px){ 
  .btnn{
    position: relative;
    left: 70px;
  }
  }